import React from "react";
import { useEffect, useState } from "react";
import { useParams } from "react-router-dom";
import styled from "styled-components";
import "../style/Detail.scss";

const NoImg = styled.div`
  flex: 1;
  width: 380px;
  height: 255px;
  border: 1px solid #ddd;
  margin: 0 0 15px 0;
  display: flex;
  align-items: center;
  justify-content: center;
`;

const Detail = ({ campingData }) => {
  const { id } = useParams();
  const [detailInfo, setDetailInfo] = useState([]);
  const detail = campingData.filter(
    (it) => parseInt(id) === parseInt(it.contentId)
  );
  useEffect(() => {
    setDetailInfo(detail);
  }, [id]);
  return (
    <div className="Detail">
      {detailInfo.map((it) => {
        return (
          <div className="infoBox" key={it.contentId}>
            <div className="topBox">
              <figure>
                {it.firstImageUrl.length > 1 ? (
                  <img src={it.firstImageUrl} alt="" />
                ) : (
                  <NoImg>  .</NoImg>
                )}
              </figure>
              <div className="textBox">
                <h3>{it.facltNm}</h3>
                <span>{it.lineIntro}</span>
                <table>
                  <tbody>
                    <tr>
                      <th></th>
                      <td>{it.addr1}</td>
                    </tr>
                    <tr>
                      <th></th>
                      <td>{it.tel}</td>
                    </tr>
                    <tr>
                      <th> </th>
                      <td>{it.lctCl}</td>
                    </tr>
                    <tr>
                      <th></th>
                      <td>
                        <a href={it.homepage} target="_blank">
                          {it.homepage}
                        </a>
                      </td>
                    </tr>
                    <tr>
                      <th> </th>
                      <td>{it.resveCl}</td>
                    </tr>
                    <tr>
                      <th> </th>
                      <td>{it.operPdCl}</td>
                    </tr>
                    <tr>
                      <th></th>
                      <td>{it.operDeCl}</td>
                    </tr>
                    <tr>
                      <th></th>
                      <td>{it.posblFcltyCl}</td>
                    </tr>
                  </tbody>
                </table>
                <button> </button>
              </div>
            </div>
            <div className="facInfo">
              <h4>  </h4>
              <p>{it.sbrsCl}</p>
              <p>  : {it.animalCmgCl}</p>
              <p>
                   :
                {it.trlerAcmpnyAt === "Y" ? "" : ""}
              </p>
              <p>
                   :{it.caravAcmpnyAt === "Y" ? "" : ""}
              </p>
            </div>
          </div>
        );
      })}
    </div>
  );
};

export default React.memo(Detail);
